.warp{
    width: 100vw;
    height: 35vw;
    display: flex;
    align-items: center;
    justify-content: left;
    background-image: linear-gradient(to right, rgb(0 0 0 / .75), rgb(0 0 0 / .5)),url(../assets/img/fondo15.jpg);
    background-size: cover;
    background-position: center;
}
.warp-text{
    padding-top: 10%;
    padding-left: 3%;
    color: rgb(246, 240, 255);
    text-align: left;
    width: 35%;
}
a{
    text-decoration: none;
}
#logos{
    size: 50%;
}
